.left-slide {
  background-color: #555555;
  background: linear-gradient(-45deg, #e7e7e7, #cfcfcf);
  background-size: cover;
  float: left; /* add this line to float the left slide to the left */
  border-radius: 10px;
  animation: gradient 15s ease infinite;
}

.contact-list-item {
  color: #000000;
  padding: 10px 10px;
  border-radius: 5px;
  vertical-align: baseline;
  display: flex;
}

.left-chat {
  border-style: inset;
  border-width: 0px;
  height: 100%;
  padding: 0px;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 100%;
  }
}

#addContactBtn:hover {
  background-color: rgb(93, 90, 174);
}

@media (max-width: 992px) {
  .whenChatIsOpen .left-slide {
    display: none; /* hide the left slide on small screens */
  }
  .right-slide {
    width: 100%; /* change the width of the right slide to 100% on small screens */
  }
}

.contact-list::-webkit-scrollbar {
  width: 0px; /* Width of the scrollbar */
  background-color: transparent; /* Background color of the scrollbar track */
}

.contact-list-item img {
  margin-right: 10px;
}

.contact-list-item {
  margin-left: 2%;
  margin-right: 2%;
  border-radius: 15px;
  background-color: #c9dbe1;
  border-width: 0px;
  border-style: solid;
  border-color: rgb(130, 130, 130);
  margin-bottom: 5px;
  transition: all 0.3s ease;
}

.contact-list-item:hover {
  border-radius: 15px;
  cursor: pointer;
}

.contact-list-item.selected {
  background-color: rgb(126, 126, 126) !important;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-width: 1px;
}

.selected:hover {
  background-color: rgb(126, 126, 126) !important;
}


.contact-list-item.selected span {
  color: rgb(226, 226, 226) !important;

}

@keyframes contact-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 100%;
  }
}

